import * as PIXI from "pixi.js";
import * as Tween from "gsap";

export class BackGround extends PIXI.Container {

    constructor() {
        super();
        this.bg1 = new PIXI.Sprite();
        this.addChild(this.bg1);
        this.bg2 = new PIXI.Sprite();
        this.addChild(this.bg2);
    }


    init(data) {
        this.bg1.texture = PIXI.Texture.from(data.bg1);
        this.bg2.texture = PIXI.Texture.from(data.bg2);
        this.bg2.alpha = 0.3;

        this.bgTween_Normal = Tween.TweenMax.to(this.bg2, 0.8,
            {
                paused: false,
                repeat: -1,
                alpha: 1,
                ease: Sine.easeInOut,
                yoyo: true,
                yoyoEase: Sine.easeInOut,
            })

        this.bgTween_High = Tween.TweenMax.to(this.bg2, 0.2,
            {
                paused: true,
                repeat: -1,
                alpha: 1,
                ease: Sine.easeInOut,
                yoyo: true,
                yoyoEase: Sine.easeInOut,
            })

    }

    play() {
        this.bg2.alpha = 0.3;
        this.bgTween_Normal.paused(true);
        this.bgTween_High.paused(false);
    }

    stop() {
        this.bg2.alpha = 0.3;
        this.bgTween_High.paused(true);
        this.bgTween_Normal.paused(false);
    }
}