import * as PIXI from "pixi.js";

export class Dice extends PIXI.Container {

    constructor() {
        super();

        this.completeCall = null;
    }

    init(data, stopDelyTime = 0) {
        this._stopDelayTime = stopDelyTime;
        this.numbers_texes = [];
        let spin_texes = [];

        for (let i = 0; i < data.number.length; i++) {
            this.numbers_texes.push(PIXI.Texture.from(data.number[i]));
        }

        for (let i = 0; i < data.spin.length; i++) {
            spin_texes.push(PIXI.Texture.from(data.spin[i]));
        }

        this.number = PIXI.Sprite.from(this.numbers_texes[Math.floor(Math.random() * this.numbers_texes.length)]);
        this.number.anchor.set(0.5);
        this.addChild(this.number);

        this.anim = new PIXI.extras.AnimatedSprite(spin_texes);
        this.anim.anchor.set(0.5);
        this.anim.loop = true;
        this.anim.animationSpeed = 0.6;
        this.addChild(this.anim);
        this.anim.visible = false;
    }

    setNumber(num) {
        let newNum = num - 1;
        if (newNum >= 0 && newNum < this.numbers_texes.length) {
            this.number.texture = this.numbers_texes[newNum];
        }
        else
            console.error("Set Pre Number error");
    }

    play() {
        this.number.visible = false;
        this.anim.visible = true;
        this.anim.play();
    }

    stop(num) {
        let newNum = num - 1;
        if (newNum < 0 && newNum >= this.numbers_texes.length) {
            console.error("Set Pre Number error");
            return;
        }

        setTimeout(() => {

            this.anim.stop();
            this.anim.visible = false;
            this.number.texture = this.numbers_texes[newNum];
            this.number.visible = true;

            if (this.completeCall != null)
                this.completeCall();

        }, this._stopDelayTime);
    }
}