import * as PIXI from "pixi.js";
import { Dice } from "./Dice";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameCommandEvent } from "../../Event/GameEvent";

export class DiceGroup extends PIXI.Container {

    init(data) {
        this._dieces = [];

        this._dice1 = new Dice();
        this._dice1.pivot.set(0.5);
        this.addChild(this._dice1);
        this._dice1.init(data, 1000);
        this._dice1.position.x = -170;
        this._dice1.visible = false;

        this._dice2 = new Dice();
        this._dice2.pivot.set(0.5);
        this.addChild(this._dice2);
        this._dice2.init(data, 1500);
        this._dice2.visible = false;

        this._dice3 = new Dice();
        this._dice3.pivot.set(0.5);
        this.addChild(this._dice3);
        this._dice3.init(data, 2000);
        this._dice3.position.x = 170;
        this._dice3.completeCall = () => this.completeSpine();
        this._dice3.visible = false;

        this._dieces.push(this._dice1, this._dice2, this._dice3);
    }

    reset() {
        for (let i = 0; i < this._dieces.length; i++) {
            this._dieces[i].visible = false;
        }
    }

    setNumbers(numbers) {
        for (let i = 0; i < numbers.length; i++) {
            if (i > this._dieces.length) {
                console.error("Dice Numbers length error");
                break;
            }

            this._dieces[i].setNumber(numbers[i]);
        }
    }

    play() {
        for (let i = 0; i < this._dieces.length; i++) {
            this._dieces[i].visible = true;
            this._dieces[i].play();
        }
    }

    stop(numbers) {
        for (let i = 0; i < numbers.length; i++) {
            if (i > this._dieces.length) {
                console.error("Dice Numbers length error");
                break;
            }

            this._dieces[i].stop(numbers[i]);
        }
    }

    completeSpine() {
        ApplicationFacade.getInstance(this.parent.Key).sendNotification(GameCommandEvent.COMPLETESPINE);
    }
}