import MVC from "puremvc";
import { ApplicationFacadeEvent } from "../../Event/ApplicationFacadeEvent";
import { Panel } from "../../Common/Panel";
import { GameProxyEvent } from "../../Event/GameEvent";
import { Tool } from "../../../Isaac/Tools/Tool";
import { GameState } from "../../Common/GameState";
import { AudioCommandEvent } from "../../Event/AudioEvent";
import { AudioManifest } from "../../../Config/AudioManifest";

export class GameProxy extends MVC.Proxy {
    static get NAME() {
        return "GameProxy";
    }

    constructor(key) {
        super(GameProxy.NAME);

        this._currState = GameState.init;
        this.initializeNotifier(key);


        this._currIssue = 0;
        this._preIssue = 0;
        this._nextTime = "00:00:00";
        this._currTime = "00:00:00";
        this._currNumber = [];
        this._preNumber = [];


        this._currCuntDownTime = 0;
        this._isStartCountDown = false;
        this._isStartGame = false;
        this._isFinshGame = false;

        // _objVgps = [];
        // _currStopCompleteNum;
        // _currGameState = "";

    }

    onRemove() {
        this._isRemvoe = true;
        this._ticker.destroy();
    }

    init(data) {
        this._currState = GameState.stop;
        this._ticker = new PIXI.ticker.Ticker();
        this._ticker.add(() => this.update());

        this._currNumber = data.preNum;
        this._assetRootSrc = data.assetRootSrc;
        this.onReady = data.onReady;
        this.onStartGame = data.onStartGame;
        this.onFinshGame = data.onFinshGame;

        this.sendNotification(ApplicationFacadeEvent.LOADPANEL, Panel.GamePanel);

        let progress_png = [];
        for (let i = 0; i < 31; i++) {
            progress_png.push(`progress (${i + 1}).png`);
        }

        this.sendNotification(GameProxyEvent.INIT, {
            background: {
                bg1: "bj.jpg",
                bg2: "bj_2.jpg"
            },
            logo: "logo.png",
            dice:
            {
                number: ["01.png", "02.png", "03.png", "04.png", "05.png", "06.png"],
                spin: ["spin_01.png", "spin_02.png", "spin_03.png", "spin_04.png", "spin_05.png", "spin_06.png"]
            },
            sound: { isMuteAll: data.isMuteAll, on: "sound_on.png", off: "sound_of.png" },
            testplay: { normal: "play_normal.png", press: "Play_press.png" },
            progress: progress_png

        });

        this.setIssue({ currIssue: data.currIssue, preIssue: data.preIssue });
        this.sendNotification(GameProxyEvent.SETWINNUMBER, this._currNumber);
        this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.BG_Sound, loop: true });

        setTimeout(() => {
            if (this.onReady != null && this.onReady != undefined)
                this.onReady();
        }, 500);
    }

    update() {
        if (this._isStartCountDown) {
            let mathTime = Math.floor(this._currCuntDownTime);
            if (mathTime > 0) {
                this._currCuntDownTime -= this._ticker.elapsedMS / 1000;

                this.sendNotification(GameProxyEvent.UPTIME, Math.floor(this._currCuntDownTime));
            }
            else {
                // this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.StateChange });

                // this._currGameState = GameState.sealplate;
                // this.sendNotification(GameProxyEvent.UPDATEGAMEINFO, { "state": this._currGameState });
                setTimeout(() => {
                    this.startGame();
                }, 500);

                this._isStartCountDown = false;
            }
        }
    }

    startGame() {

        this.sendNotification(AudioCommandEvent.STOP, AudioManifest.BG_Sound);
        this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.Run_Sound, loop: true });

        this._isStartGame = true;
        this._isStartCountDown = false;
        this._currState = GameState.seal;
        this.sendNotification(GameProxyEvent.SPINEDICE, this._currState);

        if (this.onStartGame != null && this.onStartGame != undefined)
            this.onStartGame();
    }

    simulationGame() {
        if (this._isStartGame || this._isFinshGame)
            return;

        if (this._isStartCountDown)
            if (this._currCuntDownTime <= 60)
                return;

        this.sendNotification(AudioCommandEvent.STOP, AudioManifest.BG_Sound);
        this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.Run_Sound, loop: true });

        this._currState = GameState.simulation;
        this.sendNotification(GameProxyEvent.UPSTATE, this._currState)
        this.sendNotification(GameProxyEvent.SPINEDICE);


        setTimeout(() => {
            this.sendNotification(GameProxyEvent.STOPDICE, this.getRandNumber());
        }, 1000);
    }

    completeSpine() {



        if (this._currState == GameState.open) {

            setTimeout(() => {
                // let temp = this._currIssue;
                // this.setIssue({ currIssue: Number(this._currIssue) + 1, preIssue: temp });
                this.sendNotification(GameProxyEvent.SETWINNUMBER, this._currNumber);

            }, 1000);

            setTimeout(() => {
                if (this.onFinshGame != null && this.onFinshGame != undefined)
                    this.onFinshGame();
            }, 4000);
        }
        else if (this._currState == GameState.simulation) {
            // setTimeout(() => {

            //     this.sendNotification(GameProxyEvent.SETDICE, this._currNumber);
            // }, 3000);
        }

        setTimeout(() => {
            this.resetGame();
        }, 4000);
    }


    resetGame() {

        this.sendNotification(AudioCommandEvent.STOP, AudioManifest.Run_Sound);
        this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.BG_Sound, loop: true });

        this._isStartGame = false;
        this._isFinshGame = false;
        this._currState = GameState.stop;
        this.sendNotification(GameProxyEvent.UPSTATE, this._currState);
        this.sendNotification(GameProxyEvent.RESET);
    }

    setIssue(data) {
        this._currIssue = data.currIssue;
        this._preIssue = data.preIssue;
        this.sendNotification(GameProxyEvent.SETISSUE, data);
    }

    setInfo(data) {
        this._preNumber = this._currNumber;
        this._currNumber = data.numbers;
        this._nextTime = data.nexttime;
        this._currTime = data.currtime;

        this.sendNotification(GameProxyEvent.SETWINNUMBER, this._currNumber);
        this.sendNotification(GameProxyEvent.SETPREINFO, { "nextTime": this._nextTime, "currTime": this._currTime });
    }

    setPreRank(data) {
        this._preNum = data;
        this.sendNotification(GameProxyEvent.SETPRERANK, this._preNum);
    }

    startContDown(time) {
        if (!this._isStartGame && !this._isFinshGame) {

            this._currState = GameState.counttime;
            this._currCuntDownTime = time;
            this._isStartCountDown = true;

            if (!this._ticker.started) {
                this._ticker.start();
            }
        }
    }

    mideGame() {
        if (!this._isStartGame) {

            this.startGame();

            if (this._ticker.started) {
                this._ticker.stop();
            }
        }
    }

    finshGame(numbers) {

        this.mideGame();

        this._currState = GameState.open;
        this._preNumber = this._currNumber;
        this._currNumber = numbers;

        this.sendNotification(GameProxyEvent.UPSTATE, this._currState);

        setTimeout(() => {
            this.sendNotification(GameProxyEvent.STOPDICE, this._currNumber);
        }, 1000);
    }



    getRandNumber() {
        let nums = [1, 2, 3, 4, 5, 6];
        let randomNums = [];

        for (let i = 0; i < 3; i++) {
            let rand = Math.floor(Tool.RandomRange(0, nums.length - 1));
            randomNums.push(nums[rand]);
        }

        return randomNums;
    }

    sendNotification(key, data) {
        if (!this._isRemvoe) {
            super.sendNotification(key, data)
        }
    }
}


