import * as PIXI from "pixi.js";
import { Tool } from "../../../Isaac/Tools/Tool";

export class Timer extends PIXI.Container {

    init(data) {
        this.time_te = new PIXI.extras.BitmapText("00:00:00", { font: "80px count" })
        this.time_te.anchor.set(0.5);
        this.addChild(this.time_te);

        this.progress = PIXI.extras.AnimatedSprite.fromFrames(data);
        this.progress.position.y = 10;
        this.progress.anchor.set(0.5);
        this.progress.visible = false;
        this.addChild(this.progress);
    }

    upTime(time) {
        this.time_te.text = this.conventToTime(time);
    }

    play() {
        this.progress.play();
        this.progress.visible = true;
        this.time_te.visible = false;
    }

    stop() {
        this.upTime(0);
        this.progress.stop();
        this.progress.visible = false;
        this.time_te.visible = true;
    }

    conventToTime(time) {
        let h = Tool.PadLeft(Math.floor((time / 60) / 60), 2, "0");
        let m = Tool.PadLeft(Math.floor((time / 60) % 60), 2, "0");
        let s = Tool.PadLeft(time % 60, 2, "0");
        return h + ":" + m + ":" + s
    }
}