import MVC from "puremvc";
import { GameProxyEvent } from "../../Event/GameEvent";

export class GameMediator extends MVC.Mediator {
    static get NAME() {
        return "GameMediator";
    }

    constructor(viewComponent) {
        super(GameMediator.NAME, viewComponent);
    }

    listNotificationInterests() {
        return [
            GameProxyEvent.INIT,
            GameProxyEvent.RESET,
            GameProxyEvent.SETISSUE,
            GameProxyEvent.SETPRERANK,
            GameProxyEvent.SETWINNUMBER,
            GameProxyEvent.SETPREINFO,
            GameProxyEvent.UPTIME,
            GameProxyEvent.UPSTATE,
            GameProxyEvent.SETDICE,
            GameProxyEvent.SPINEDICE,
            GameProxyEvent.STOPDICE
        ];
    }


    handleNotification(notification) {
        switch (notification.getName()) {
            case GameProxyEvent.INIT:
                this.View.init(notification.getBody());
                break;
            case GameProxyEvent.RESET:
                this.View.BackGround.stop();
                this.View.Timer.stop();
                this.View.DiceGroup.reset();
                break;
            case GameProxyEvent.SETISSUE:
                // this.View.WinningNumber.setIssue(notification.getBody());
                this.View.Issue.setIssue(notification.getBody());
                break;
            case GameProxyEvent.SETPRERANK:
                this.View.WinningNumber.setNumbers(notification.getBody());
                break;
            case GameProxyEvent.SETWINNUMBER:
                this.View.DiceGroup.setNumbers(notification.getBody());
                this.View.WinningNumber.setNumbers(notification.getBody());
                break;
            case GameProxyEvent.SETPREINFO:
                this.View.NextIssueInfo.upInfo(notification.getBody());
                break;
            case GameProxyEvent.UPTIME:
                this.View.Timer.upTime(notification.getBody());
                break;
            case GameProxyEvent.UPSTATE:
                this.View.upState(notification.getBody());
                break;
            case GameProxyEvent.SETDICE:
                this.View.DiceGroup.setNumbers(notification.getBody());
                break;
            case GameProxyEvent.SPINEDICE:
                this.View.DiceGroup.play();
                this.View.BackGround.play();
                this.View.Timer.play();
                break;
            case GameProxyEvent.STOPDICE:
                this.View.DiceGroup.stop(notification.getBody());
                break;
            default:
                break;
        }
    }

    /**
     * @typedef {import("../Panel/GamePanel").GamePanel} GamePanel
     * @returns {GamePanel}
     */
    get View() {
        return this.viewComponent;
    }
}