import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameCommandEvent } from "../../Event/GameEvent";

export class TestPlayBtn extends PIXI.Container {

    init(data) {
        this.normal_tex = PIXI.Texture.from(data.normal);
        this.press_tex = PIXI.Texture.from(data.press);
        this.btn = new PIXI.Sprite(this.normal_tex);
        this.btn.anchor.set(0.5);
        this.btn.interactive = true;
        this.btn.buttonMode = true;
        this.addChild(this.btn);

        this.btn.on("pointerdown", () => {
            ApplicationFacade.getInstance(this.parent.Key).sendNotification(GameCommandEvent.SIMULATION);
        })
    }
}