import * as PIXI from "pixi.js";
import { Tool } from "../../../Isaac/Tools/Tool";

export class WinningNumber extends PIXI.Container {

    init() {

        // this.issue_te = new PIXI.Text("000", {
        //     fontFamily: 'Arial',
        //     fill: ['#ffffff'],
        //     fontSize: 25,
        // });
        // this.issue_te.anchor.set(0.5);
        // this.issue_te.position.y = -32.5;
        // this.addChild(this.issue_te);

        this.number_te = new PIXI.extras.BitmapText("0 + 1 + 2 = 3", { font: "100px num" });
        this.number_te.anchor.set(0.5);
        this.number_te.position.x = -60;
        this.number_te.position.y = 10;
        this.addChild(this.number_te);

        this.smallbig_te = new PIXI.Text("大", {
            fontFamily: 'Arial',
            fontWeight: 'bold',
            fontSize: 45,
            fill: ['#ffff00']
        })
        this.smallbig_te.anchor.set(0.5);
        this.smallbig_te.position.x = 190;
        this.addChild(this.smallbig_te);
    }

    setIssue(number) {
        this.issue_te.text = Tool.PadLeft(number, 3, "0");
    }

    /**
     * 
     * @param {Number[]} numbers 
     */
    setNumbers(numbers) {
        if (numbers.length < 3)
            console.error("Set Text Number Error");

        let sum = numbers[0] + numbers[1] + numbers[2];
        this.number_te.text = `${numbers[0]} + ${numbers[1]} + ${numbers[2]} = ${sum}`;

        if (sum < 11)
            this.smallbig_te.text = "小";
        else
            this.smallbig_te.text = "大";

    }
}