import MVC from "puremvc";
import { GameProxy } from "../../Mode/Proxy/GameProxy";
import { GameCommandEvent } from "../../Event/GameEvent";

export class GameCommand extends MVC.SimpleCommand {
    static get NAME() {
        return "GameCommand";
    }

    constructor(key) {
        super();

        this.initializeNotifier(key);
    }

    register() {
        let keys = [
            GameCommandEvent.INIT,
            GameCommandEvent.SETISSUE,
            GameCommandEvent.SETPREINFO,
            GameCommandEvent.SETPRERANK,
            GameCommandEvent.STARTTIMECOUNTDOWN,
            GameCommandEvent.MIDGAME,
            GameCommandEvent.FINSHGAME,
            GameCommandEvent.SIMULATION,
            GameCommandEvent.COMPLETESPINE
        ];

        keys.forEach(key => {
            this.getFacade().registerCommand(key, GameCommand);
        });
    }

    execute(notification) {
        switch (notification.getName()) {
            case GameCommandEvent.INIT:
                this.Proxy.init(notification.getBody());
                break;
            case GameCommandEvent.SETISSUE:
                this.Proxy.setIssue(notification.getBody());
                break;
            case GameCommandEvent.SETPREINFO:
                this.Proxy.setInfo(notification.getBody());
                break;
            case GameCommandEvent.SETPRERANK:
                this.Proxy.setPreRank(notification.getBody());
                break;
            case GameCommandEvent.STARTTIMECOUNTDOWN:
                this.Proxy.startContDown(notification.getBody());
                break;
            case GameCommandEvent.MIDGAME:
                this.Proxy.mideGame();
                break;
            case GameCommandEvent.FINSHGAME:
                this.Proxy.finshGame(notification.getBody());
                break;
            case GameCommandEvent.SIMULATION:
                this.Proxy.simulationGame();
                break;
            case GameCommandEvent.COMPLETESPINE:
                this.Proxy.completeSpine();
                break;
            default:
                break;
        }

    }

    /**
     * @returns {GameProxy}
     */
    get Proxy() {
        return this.getFacade().retrieveProxy(GameProxy.NAME);
    }
}