import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameMediator } from "../Mediator/GameMediator";
import { BackGround } from "../Component/BackGround";
import { Dice } from "../Component/Dice";
import { TestPlayBtn } from "../Component/TestPlayBtn";
import { SoundBtn } from "../Component/SoundBtn";
import { Timer } from "../Component/Timer";
import { NextIssueInfo } from "../Component/NextIssueInfo";
import { WinningNumber } from "../Component/WinningNumber";
import { DiceGroup } from "../Component/DiceGroup";
import { GameState } from "../../Common/GameState";
import { Shap } from "../Component/Shap";
import { Issue } from "../Component/Issue";

export class GamePanel extends PIXI.Container {
    constructor(data) {
        super();

        this._key = data.key;
        this._stageWidth = data.width;
        this._stageHeight = data.height;

        this._background = new BackGround();
        this.addChild(this._background);

        this._logo = new PIXI.Sprite();
        this.addChild(this._logo);

        this._diceGroup = new DiceGroup();
        this.addChild(this._diceGroup);

        this._dice1 = new Dice();
        this._dice1.pivot.set(0.5);
        this.addChild(this._dice1);
        this._dice2 = new Dice();
        this._dice2.pivot.set(0.5);
        this.addChild(this._dice2);
        this._dice3 = new Dice();
        this._dice3.pivot.set(0.5);
        this.addChild(this._dice3);

        this._testPlayBtn = new TestPlayBtn();
        this._testPlayBtn.pivot.set(0.5);
        this.addChild(this._testPlayBtn);

        this._frame = new Shap(0x000000, 0.5, this._stageWidth, this._stageHeight * 0.09);
        this._frame.position.y = this._stageHeight * 0.91;
        this.addChild(this._frame);
        this._frame.visible = true;

        this._issue = new Issue();
        this.addChild(this._issue);

        this._winningNumber = new WinningNumber();
        this.addChild(this._winningNumber);

        this._nextInfo = new NextIssueInfo();
        this.addChild(this._nextInfo);

        this.infoText = new PIXI.Text("倒计时", {
            fontFamily: 'Arial',
            fontSize: 30,
            fill: ['#ffffff'],
        });

        this.infoText.anchor.set(0.5);
        this.infoText.position.set(this._stageWidth * 0.5, this._stageHeight * 0.35);
        this.addChild(this.infoText);

        this._timer = new Timer();
        this.addChild(this._timer);

        this._soundBtn = new SoundBtn();
        this.addChild(this._soundBtn);

        this.once("added", () => this.onAdded());
    }

    onAdded() {
        ApplicationFacade.getInstance(this._key).registerMediator(new GameMediator(this));
    }

    init(data) {
        this._background.init(data.background);
        // this._logo.texture = PIXI.Texture.from(data.logo);
        // this._logo.position.set(25, 25);
        // this._logo.interactive = true;
        // this._logo.buttonMode = true;

        this._diceGroup.init(data.dice);
        this._diceGroup.position.set(this._stageWidth * 0.5, this._stageHeight * 0.74);

        this._issue.init();

        this._winningNumber.init();
        this._winningNumber.position.set(this.StageWidth * 0.5, this._stageHeight * 0.95);

        // this._nextInfo.init();
        // this._nextInfo.position.set(this._stageWidth * 0.825, this._stageHeight * 0.06);

        this._testPlayBtn.init(data.testplay);
        this._testPlayBtn.scale.set(0.55);
        this._testPlayBtn.position.set(this._stageWidth * 0.49, this._stageHeight * 0.885);

        this._timer.init(data.progress);
        this._timer.position.set(this._stageWidth * 0.5, this._stageHeight * 0.43);

        this._soundBtn.init(data.sound);
        this._soundBtn.position.set(this._stageWidth * 0.83, this._stageHeight * 0.06);
    }

    upState(state) {
        switch (state) {
            case GameState.seal:
            case GameState.open:
                this.infoText.text = "开奖中";
                break;
            case GameState.simulation:
                this.infoText.text = "模似开奖";
                break;
            default:
                this.infoText.text = "倒计时";
                break;
        }
    }

    remove() {

    }

    get Key() {
        return this._key;
    }

    get StageWidth() {
        return this._stageWidth;
    }

    get StageHeight() {
        return this._stageHeight;
    }

    get WinningNumber() {
        return this._winningNumber;
    }

    get NextIssueInfo() {
        return this._nextInfo;
    }

    get Timer() {
        return this._timer;
    }

    get DiceGroup() {
        return this._diceGroup;
    }

    get BackGround() {
        return this._background;
    }

    get Issue() {
        return this._issue;
    }
}